import React, { Component } from 'react';

import { Grid } from 'semantic-ui-react';
import ModeHeader from './ModeHeader';
import StatsHeader from './StatsHeader';
import Player from './Player';

export default class Board extends Component {
    constructor(props) {
        super(props);

        this.state = { mode: 'solo' };
    }

    handleModeChange = direction => {
        const { mode } = this.state;
        let nextMode = '';
        switch (mode) {
            case 'solo':
                nextMode = direction === 'forward' ? 'duo' : 'squad';
                break;
            case 'duo':
                nextMode = direction === 'forward' ? 'squad' : 'solo';
                break;
            case 'squad':
                nextMode = direction === 'forward' ? 'solo' : 'duo';
                break;
            default:
                break;
        }
        this.setState({ mode: nextMode });
    };

    render() {
        const { players, removePlayer } = this.props;
        const { mode } = this.state;
        const columns = [
            { header: 'Player', accessor: 'username', size: '5' },
            { header: 'K/D', accessor: 'kd', size: '2' },
            { header: 'Win %', accessor: 'winrate', size: '2' },
            { header: 'Kills', accessor: 'kills', size: '2' },
            { header: 'Wins', accessor: 'placetop1', size: '2' }
            // { header: 'Matches', accessor: 'matchesplayed', size: '3' }
        ];
        return (
            <Grid style={{ backgroundColor: '#2c3c57', color: 'white' }}>
                {/* Render Mode Header / Selector */}
                <ModeHeader mode={mode} handleModeChange={this.handleModeChange} />

                {/* Render Column Header */}
                <StatsHeader columns={columns} />

                {/* Render Player stats */}
                {players.map((player, index) => (
                    <Player
                        columns={columns}
                        player={player}
                        key={index}
                        mode={mode}
                        boardIndex={index}
                        removePlayer={removePlayer}
                    />
                ))}
            </Grid>
        );
    }
}
